import { Injectable } from '@angular/core';
import { Observable } from 'rxjs/Observable';
import { HttpClient, HttpHeaders } from '@angular/common/http';

const apiKey = 'EC2BC390-396B-446F-B371-9A2AFDDD19F1';
const headers = new HttpHeaders().set('X-CoinAPI-Key', apiKey);
const exchangesUrl = 'https://rest.coinapi.io/v1/exchanges';
const assetsUrl = 'https://rest.coinapi.io/v1/assets';
const ohlcvUrl = 'https://rest.coinapi.io/v1/ohlcv/BITSTAMP_SPOT_BTC_USD/latest?period_id=1DAY&limit=1000';
const klinesUrl = 'http://localhost:3000/api/candle'; // 'https://api.binance.com/api/v1/klines?symbol=BTCUSDT&interval=1M';

export abstract class AbstractRequests {
  constructor(protected url, protected http: HttpClient) {}

  get(): Observable<any[]> {
    return this.http.get<any>(this.url, {headers});
  }
}

@Injectable({
  providedIn: 'root'
})
export class ExchangesService extends AbstractRequests {
  constructor(protected http: HttpClient) {
    super(exchangesUrl, http);
  }
}

@Injectable({
  providedIn: 'root'
})
export class AssetsService extends AbstractRequests {
  constructor(protected http: HttpClient) {
    super(assetsUrl, http);
  }
}

@Injectable({
  providedIn: 'root'
})
export class OHLCVService extends AbstractRequests {
  constructor(protected http: HttpClient) {
    super(ohlcvUrl, http);
  }
}

@Injectable({
  providedIn: 'root'
})
export class KlinesService extends AbstractRequests {
  constructor(protected http: HttpClient) {
    super(klinesUrl, http);
  }
}
